.statisticsWrapper{
    height: 320px;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0px 0px 10px #444343;
    overflow: hidden;
    color: #848587;
}

.title{
    margin-bottom: 28px;
    padding: 30px 0;
    color: #000000;
    font-weight: 600;
    font-size: 30px;
}

.statistics:not(:last-child){
    padding-bottom: 16px;
}

.statList{
  display: flex;
  justify-content: space-evenly;
  background-color: #f3f6f9;
  height: 88px;
  color: #ffffff;
  font-weight: 600;

}

.statList > li {
  justify-content: center;
  display: flex;
  flex-direction: column;
  flex: 0 1 100%;
  background-color: rgb(171, 231, 31);
  box-shadow:  0px 0px 10px #444343;  
  
}